<template>
  <div>
    <van-datetime-picker
      v-model="currentDate"
      type="date"
      title=""
      :min-date="minDate"
      :max-date="maxDate"
      @cancel="onCancel=$emit('close')"
      @confirm="onConfirm"
    />
  </div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
import dayjs from 'dayjs'
export default {
  name: 'UpdateBirthday',
  props: {
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      minDate: new Date(1900, 1, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(this.value),
      birthday: this.value
    }
  },
  methods: {
    // 
    async onConfirm (value) {
      this.$toast.loading({
        message: '...',
        forbidClick: true, // 
        duration: 0 // toast
      })
      // console.log(value, 'bir')
      // dayjs
      const birTemp = dayjs(value).format('YYYY-MM-DD')
      // console.log(birTemp)
      try {
        await updateUserProfileAPI({
          birthday: birTemp
        })

        // 
        this.$emit('input', birTemp)
        // 
        this.$emit('close')
        this.$toast('')
      } catch (err) {
        this.$toast('')
      }
    }
  }
}
</script>

