<template>
  <div class="update-gender">
    <!--  -->
      <van-picker
        title=""
        show-toolbar
        :columns="columns"
        @confirm="onConfirm"
        @cancel="onCancel=$emit('close')"
        @change="onChange"
        :default-index="value"
      />
  </div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
/*
  01 , 
  02 ui, , , 
  03 , 
  04 ,()
*/

export default {
  name: 'UpdateGender',
  props: {
    value: {
      type: Number,
      default: 0
    }
  },

  data () {
    return {
      columns: ['', ''],
      gender: this.value
    }
  },

  methods: {
    // 
    async onConfirm () {
      this.$toast.loading({
        message: '...',
        forbidClick: true, // 
        duration: 0 // toast
      })
      try {
        await updateUserProfileAPI({
          gender: this.gender
        })

        // 
        this.$emit('input', this.gender)
        // 
        this.$emit('close')
        this.$toast('')
      } catch (err) {
        this.$toast('')
      }
    },

    onChange (picker, value, index) {
      /*
        picker 
        value 
        index 

      */
      this.gender = index
      console.log(index, '123')
    }
  }
}
</script>

<style lang="less" scoped>
.update-gender {
  .van-nav-bar {
    background-color: #fff;
    :deep(.van-nav-bar__title) {
      color: #000;
    }

    .van-icon {
      color: #3296fa;
    }
  }

  .update-name-field {
    margin: 20px;
  }

  .van-button {
    background-color: #fff;
    color: #000;
    border: none;
  }
}
</style>
