<template>
  <div class="update-name">
    <!--  -->
    <van-nav-bar
      title=""
      left-text=""
      right-text=""
      @click-left="$emit('close')"
      @click-right="onConfirm"
    />

    <!--  -->
    <div class="update-name-field">
      <van-field
        v-model.trim="localName"
        rows="2"
        autosize
        type="textarea"
        maxlength="10"
        placeholder=""
        show-word-limit
      />
    </div>
  </div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
export default {
  name: 'UpdateName',
  props: {
    value: { // v-model , $emit('input')
      type: String,
      default: ''
    }
  },
  data () {
    return {
      localName: this.value
    }
  },

  methods: {
    // 
    async onConfirm () {
      this.$toast.loading({
        message: '...',
        forbidClick: true, // 
        duration: 0 // toast
      })
      try {
        // 
        if (!this.localName.length) {
          this.$toast('')
          return
        }
        await updateUserProfileAPI({
          name: this.localName
        })

        //  - v-model 
        this.$emit('input', this.localName)

        // 
        this.$emit('close')
        this.$toast.sccess('')
      } catch (err) {
        if (err.response && err.response.status === 409) {
          this.$toast.fail('')
        } else {
          this.$toast('')
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.update-name {
  .van-nav-bar {
    background-color: #fff;
    :deep(.van-nav-bar__title) {
      color: #000;
    }

    .van-icon {
      color: #3296fa;
    }
  }

  .update-name-field {
    margin: 20px;
  }
}
</style>
