<template>
  <div class="update-photo">
    <img :src=value class="img" ref="img">
    <div class="toolbar">
      <span class="cancel" @click="$emit('close')"></span>
      <span class="confirm" @click="onConfirm"></span>
    </div>
  </div>
</template>

<script>
import { updateUserPhotoAPI } from '../../../api/index.js'

import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'

export default {
  name: 'UpdatePhoto',
  props: {
    // object({}) null
    value: { // blob
      type: String,
      default: ''
    }
  },

  data () {
    return {
      cropper: null
    }
  },
  mounted () {
    this.initCropper()
  },
  methods: {
    initCropper () {
      // 
      const image = this.$refs.img
      this.cropper = new Cropper(image, {
        viewMode: 1, // 0 1,2,3
        dragMode: 'move', //  move: 
        aspectRatio: 1, // , 1:11
        autoCropArea: 1, // 
        cropBoxMovable: false, // 
        cropBoxResizable: false, // 
        background: false, // 
        movable: true // true
        // crop (event) {
        // const eventObj = { // 
        //     x: event.detail.x,
        //     y: event.detail.y,
        //     width: event.detail.width,
        //     height: event.detail.height,
        //     rotate: event.detail.rotate,
        //     scaleX: event.detail.scaleX,
        //     scaleY: event.detail.scaleY,
        //     detail: event.detail
        //   }
        //   console.log(eventObj)
        // }
      })
    },

    onConfirm () {
      //  
      // 01 
      console.log(this.cropper.getData())
      // 02 

      // cropperjs, 
      //  this.cropper.getCroppedCanvas().toBlob() 
      this.cropper.getCroppedCanvas().toBlob((blob) => {
        console.log(blob)
        // blob, 
        this.updateUserPhoto(blob)
      })
    },

    async updateUserPhoto (blob) {
      const formData = new FormData()
      formData.append('photo', blob)

      // , 
      this.$toast.loading({
        message: '...',
        forbidClick: true, // 
        duration: 0 // toast
      })

      try {
        const { data: res } = await updateUserPhotoAPI(formData)

        // 
        this.$toast.success('')

        // 
        //  - 
        this.$emit('update-photo', res.data.photo)

        // 
        this.$emit('close')
      } catch (err) {
        this.$toast.fail('')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.update-photo {
  background-color: #000;
  height: 100%;

  .toolbar {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;

    .cancel, .confirm {
      font-size: 40px;
      margin: 12px;
    }
  }

  .img {
    display: block;
    max-width: 100%;
  }
}
</style>
