<template>
  <div class="user-profile">
    <!--  -->
    <van-nav-bar
      title=""
      left-arrow
      @click-left="$router.back()"
      fixed
    />

    <!--  -->
    <div class="main-wrap">
      <!--  $refs ref -->
      <van-cell title="" is-link @click="$refs['file'].click()">
        <template #default>
          <van-image
            class="avatar"
            round
            fit="cover"
            :src="user.photo"
          />
        </template>
      </van-cell>
      <van-cell title="" is-link :value="user.name" @click="isShowUpdateName = true"></van-cell>
      <van-cell title="" is-link :value="user.gender === 0 ? '' : ''" @click="isShowUpdateGender = true"></van-cell>
      <van-cell title="" is-link :value="user.birthday" @click="isShowUpdateBirthday = true"></van-cell>
    </div>

    <!--  -->
    <van-popup
      v-model="isShowUpdateName"
      position="bottom"
      :style="{ height: '100%' }"
    >
      <UpdateName @close="isShowUpdateName = false" v-model="user.name"></UpdateName>
    </van-popup>

    <!--   -->
    <van-popup
      v-model="isShowUpdateGender"
      position="bottom"
      :style="{ height: '50%' }"
    >
      <UpdateGender @close="isShowUpdateGender = false" v-model="user.gender"></UpdateGender>
    </van-popup>

    <!--   -->
    <van-popup
      v-model="isShowUpdateBirthday"
      position="bottom"
      :style="{ height: '50%' }"
    >
      <UpdateBirthday @close="isShowUpdateBirthday = false" v-model="user.birthday"></UpdateBirthday>
    </van-popup>

    <!--  -->
    <!--  -->
    <input type="file" hidden ref="file" @change="onFileChange" />
    <!--   -->
    <van-popup
      v-model="isShowUpdatePhoto"
      position="bottom"
      :style="{ height: '100%' }"
    >

      <!-- v-model="image"  -->
      <!--  @update-photo="user.photo = $event" , - onUpdatePhoto -->
      <UpdatePhoto
        @close="isShowUpdatePhoto = false"
        v-model="image"
        v-if="isShowUpdatePhoto"
        @update-photo="onUpdatePhoto"
      ></UpdatePhoto>
    </van-popup>

  </div>
</template>

<script>
import { getUserProfileAPI } from '../../api/index.js'
import UpdateName from './components/update-name.vue'
import UpdateGender from './components/update-gender.vue'
import UpdateBirthday from './components/update-birthday.vue'
import UpdatePhoto from './components/update-photo.vue'

export default {
  name: 'userProfile',
  components: {
    UpdateName,
    UpdateGender,
    UpdateBirthday,
    UpdatePhoto

  },

  data () {
    return {
      user: {},
      isShowUpdateName: false,
      isShowUpdateGender: false,
      isShowUpdateBirthday: false,
      isShowUpdatePhoto: false,
      image: null
    }
  },

  created () {
    this.loadProfile()
  },

  methods: {
    async loadProfile () {
      try {
        const { data: res } = await getUserProfileAPI()
        this.user = res.data
        console.log(this.user, 'user')
      } catch (err) {
        this.$toast(' ')
      }
    },

    // 
    onFileChange () {
      // 
      const file = this.$refs.file.files[0]
      // console.log(this.$refs.file.files)

      // blob
      this.image = window.URL.createObjectURL(file)

      // value, 
      this.$refs.file.value = ''

      // 
      this.isShowUpdatePhoto = true
    },

    // userphoto
    onUpdatePhoto (newPhoto) {
      this.user.photo = newPhoto
    }
  }
}
</script>

<style lang="less" scoped>
.user-profile {
  .main-wrap {
    padding-top: 92px;
  }

  .avatar {
    width: 52px;
    height: 52px;
  }
}
</style>
